import React from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import './HomePageButtonLink.scss';

const HomePageButtonLink = props => {
  const { animationStart, imgSrc, imgAltText, label, animateAllIcons, linkHref } = props;
  const btnClass = animationStart
    ? cx('button', 'hp--ratio--button', 'animation', animationStart)
    : cx('button', 'hp--ratio--button');

  return (
    <button type="button" data-href={linkHref} className={btnClass} onClick={animateAllIcons}>
      <img className="icon" src={imgSrc} alt={imgAltText} />
      <div className="hp--button--label">{label}</div>
    </button>
  );
};

HomePageButtonLink.propTypes = {
  animationStart: PropTypes.string,
  linkHref: PropTypes.string,
  imgSrc: PropTypes.string,
  imgAltText: PropTypes.string,
  label: PropTypes.string,
  animateAllIcons: PropTypes.func.isRequired,
};

HomePageButtonLink.defaultProps = {
  animationStart: null,
  linkHref: null,
  imgSrc: null,
  imgAltText: null,
  label: null,
};

export default HomePageButtonLink;
